  line-height: 1.1;
  font-family: "Google Sans Regular";
  text-align: center;
}

.certs-body-div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

@media (max-width:1380px){
    .certs-header{
        font-size: 35px;
    }
}

@media (max-width:768px){
    .certs-body-div{
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .certs-header{
        font-size: 30px;
    }
}


