.os-charts-header {
    font-size: 40px;
  line-height: 1.1;
  font-family: "Google Sans Regular";
  text-align: center;
  margin-left: 10px;
  margin-right: 10px;
}

.os-charts-body-div {
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    /* table-layout: fixed; */
    width: 100%;
}
/* 
.tb-item {
    display: table-cell;
} */

@media (max-width:1380px){
    .os-charts-header{
        font-size: 35px;
    }
}

@media (max-width:768px){
    .os-charts-header{
        font-size: 25px;
    }

    .os-charts-body-div {
        -ms-flex-direction: column;
            flex-direction: column;
    }
}


